/************************
*  Стили для задачи №1  *
************************/

/*
*  Навигационные кнопки 
*/
.toolbar-list {
  display: flex;
  width: 100%;
  margin-top: 20px;
  gap: 10px;
}

.toolbar-item {
  padding: 5px 10px;
  list-style-type: none;
  white-space: nowrap;

  border: 1px solid #808080;
  border-radius: 4px;

  cursor: pointer;
  
}

.toolbar-item__selected {
  color: #ffffff;
  background-color: #808080;
}

.toolbar-item:hover {
  box-shadow: 1px 1px 3px 1px #808080;

}

/*
*  Блок с картами проектов
*/
.project-list {
  column-count: 3;
  column-gap: 10px;
  column-fill: balance;
  column-width: auto;
  max-width: 100%;
  margin: 10px auto 0;
  min-height: 580px;
}

.project-list-item {
  width: 100%;
  margin-top: 6px;
  float: left;
  page-break-inside: avoid;
  break-inside: avoid;
}

@media (max-width: 850px) {
  .project-list {
    column-count: 2;
  }
}

@media (max-width: 550px) {
  .project-list {
    column-count: 1;
  }
}
