/***************************************
*  Стиль shopItem для задачи №2        *
*  Используется компонентом ShopItem   *
***************************************/

.item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-width: 343px;
  width: 100%;
  gap: 10px;

  padding: 15px;

  background-color: #f5f5f5;;
  /* box-shadow: 0 5px 10px 1px #808080; */
}

.item-img {
  width: 100px;
  height: 65px;

  margin-top: -10px;

  object-fit: cover;
}

.item-price {
  color: #ef0000;
}

.item-btn {
  padding: 4px 8px;
  border: 1px solid #ef0000;
  border-radius: 14px;
  min-width: 59px;

  color: #ef0000;
  font-weight: 600;

  cursor: pointer;
}

.item-btn:hover {
  box-shadow: 1px 1px 4px 1px #808080;
}

@media (max-width: 550px) {
  .item {
    padding: 5px;
  }
}