/***************************************
*  Стиль shopCard для задачи №2        *
*  Используется компонентом ShopCard   *
***************************************/

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 300px;
  width: 300px;
  height: 300px;

  padding: 15px;

  background-color: #f5f5f5;;
  box-shadow: 0 5px 10px 1px #808080;
}

.card-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  object-fit: cover;

  z-index: 1;
}

.card-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  z-index: 2;
}

.card-name {
  text-transform: uppercase;
}

.card-price {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 195px;
  padding: 0 20px;

  color: #ef0000;
}

.card-btn {
  padding: 4px 8px;
  border: 1px solid #ef0000;
  border-radius: 14px;

  color: #ef0000;
  font-weight: 600;

  cursor: pointer;
}

.card-btn:hover {
  box-shadow: 1px 1px 4px 1px #808080;
}
